<template>
    <v-container>

        <!-- Ficha de una entrega -->
        <v-card color="blue" v-if="entregaSeleccionada != null" class="my-3">
            <v-row><v-col class="mx-3"><v-btn @click="clickEnVerMapa()" color="green" block dark><div class="itemUnaEntrega">Ver en el mapa</div></v-btn></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega"># {{entregaSeleccionada.OrdenEntrega}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.Comprobante}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.NombreCliente}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.NombreDestino}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.Domicilio}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.Localidad}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">Bultos: {{entregaSeleccionada.Bultos}}</div></v-col></v-row>
            <v-row ><v-col class="mx-3"><v-btn @click="clickEnOK()" color="green" block dark><div class="itemUnaEntrega">OK 👍</div></v-btn></v-col></v-row>
            <v-row ><v-col class="mx-3"><v-btn @click="clickEnNo()" color="red" block dark><div class="itemUnaEntrega">NO 👎</div></v-btn></v-col></v-row>
            <v-row ><v-col class="mx-3"><v-btn @click="clickEnParcial()" color="orange" block dark><div class="itemUnaEntrega">PARCIAL 🤔</div></v-btn></v-col></v-row>
            <v-row><v-col class="mx-3"><v-btn @click="clickEnCancelar()" color="rgb(37, 150, 190)" block dark><div class="itemUnaEntrega"><v-icon>mdi-keyboard-backspace</v-icon> Volver a la lista</div></v-btn></v-col></v-row>
        </v-card>


        <!-- Lista de entregas pendientes -->        
        <v-row class="mb-1"><v-col><v-btn @click="clickEnActualizar()" color="green" block dark v-if="entregaSeleccionada==null"><div class="itemUnaEntrega">Actualizar<v-icon>mdi-refresh</v-icon></div></v-btn></v-col></v-row>
        <v-alert color="blue" class="py-0 mt-0 mb-1 itemEntrega" v-if="entregaSeleccionada==null">{{getTituloListaEntregas()}}</v-alert>
        <v-alert @click="clickEnEntrega(item)" color="blue" v-for="(item, index) in this.listaEntregas" class="my-1" v-if="entregaSeleccionada==null">
            <v-row class="py-0 my-0">
                <v-col align="center" class="py-0 my-0">
                    <div class="itemEntrega"># {{item.OrdenEntrega}} - {{item.NombreCliente}}</div>
                </v-col>
            </v-row>
            <v-row class="py-0 my-0">
                <v-col align="center" class="py-0 my-0">
                    <div class="itemEntrega">{{item.Comprobante}}</div>
                </v-col>
            </v-row>
            <v-row class="py-0 my-0">
                <v-col align="center" class="py-0 my-0">
                    <div class="itemEntrega">{{item.NombreDestino}}</div>
                </v-col>
            </v-row>
        </v-alert>

    </v-container>
</template>

<style scoped>
    .itemUnaEntrega {
        color: white;
        font-size: 16px;
        font-weight: 900;
        font-family: "Helvetica Neue";
    }
    .itemEntrega {
        color: white;
        font-size: 16px;
        font-weight: 900;
        font-family: "Helvetica Neue";
    }
</style>

<script>
import choferes from "@/store/choferes"
import store from "@/store"
import fechas from "vue-lsi-util/fechas"


export default {
    name: "VerRutaActual",

    data() {
        return {
            listaEntregas: [],
            entregaSeleccionada: null
        }
    },

    methods: {
        getTituloListaEntregas() {
            if (this.listaEntregas.length>0) {
                return fechas.getHoy()+" - "+this.listaEntregas.length+" entregas - V: "+this.listaEntregas[0].Ventana
            } else {
                return fechas.getHoy()+" - Sin entregas para hoy"
            }
        },
        getEntregas() {            
            choferes.actions.getEntregasPendientes(store.state.choferes.choferActual.numero, fechas.getHoy()) 
                .then(respuesta => {
                   this.listaEntregas=respuesta
                   this.listaEntregas.sort((a, b) => {
                       if (a.OrdenEntrega>b.OrdenEntrega) {
                           return 1
                       } else {
                           return -1
                       }
                   })
                })
                .catch(puteada => {
                    console.log(puteada)
                })
        },
        clickEnActualizar() {
            this.getEntregas()
        },
        clickEnVerMapa() {
            window.open("http://maps.google.com/maps?q="+this.entregaSeleccionada.Latitud+","+this.entregaSeleccionada.Longitud, "_blank").focus()
        },
        clickEnCancelar() {
            this.entregaSeleccionada=null
        },
        clickEnOK() {
            this.registrarEntregaEntregada("ENTREGADO")
        },
        clickEnNo() {
            this.registrarEntregaEntregada("NO ENTREGADO")
        },
        clickEnParcial() {
            this.registrarEntregaEntregada("PARCIAL")
        },

        registrarEntregaEntregada(estado) {
            const confirmacion={
                titulo: estado+" - Confirma?", 
                mensaje: "Confirma la registración del estado <b>"+estado+"</b> para el despacho seleccionado?", 
                botonPrimario: "OK", 
                botonSecundario: "Cancelar",
                callback: respuesta => {
                    if (respuesta=="OK") {
                        this.registrarEntregaEntregadaConfirmado(estado)
                    }
                }
            }
            store.dispatch("alertDialog/mostrar", confirmacion)
        },

        registrarEntregaEntregadaConfirmado(estado) {

            choferes.actions.registrarEntregaEntregada(this.entregaSeleccionada.Guia, 
                                                       store.state.choferes.choferActual.numero,
                                                       fechas.getHoy(), 
                                                       estado)
            .then(respuesta => {
                const ubicacionEntrega=this.listaEntregas.indexOf(this.entregaSeleccionada)
                this.listaEntregas.splice(ubicacionEntrega, 1)
                this.entregaSeleccionada=null
            })
            .catch(puteada=> {
                this.mostrarError(puteada)
            })

        },
        clickEnEntrega(item) {
            this.entregaSeleccionada=item
        },
        mostrarError(mensaje) {
            store.dispatch("snackbar/mostrar", mensaje)
        }
    },

    created() {
        this.getEntregas()
    }
}
</script>